<template lang="pug">
  .range-sex
    img(src="../../assets/images/character_editor/range_sex/girl.svg")
    vue-range-slider.range-sex__slider(v-model="position", :min="0", :max="100", :class="{ reverse: !reverse }")
    img(src="../../assets/images/character_editor/range_sex/man.svg")
</template>

<script>
import VueRangeSlider from 'vue-range-slider'
require('../../assets/style/form.stylus')

export default {
  props: ['title'],
	components: {
    VueRangeSlider
	},
	data() {
		return {
      position: 50,
      value: 0,
      reverse: true
		}
  },
  
  created() {
    setTimeout(() => {
      const value = window.getInterface('CharacterEditor').getRandomValue(0, 100);
      this.position = value;
    }, 100);
  },

	methods: {
 	},

  watch: {
    position(val) {
      if (val > 50) this.reverse = false;
      else this.reverse = true;
      
      this.value = (val - 50) / 50;

      this.$emit('value', this.value);

      let data = window.getInterface('CharacterEditor').getData(this.title);
      mp.trigger(window.getInterface('CharacterEditor').$data.METHOD_NAME, JSON.stringify(data));
    }
  }
};
</script>

<style lang="stylus">
.range-sex
  display flex
  justify-content space-between

  &__slider
    width calc(100% - 76px)
    &.reverse
      .range-slider
        &-rail
          background #1E2036

          &::after
            background #0671FF

        &-fill
          background linear-gradient(115.2deg, #2982FE 0%, #1266DA 100%) !important

        &-inner
          &::after
            display none

          &::before
            display block

    .range-slider
      &-rail
        height 8px
        background linear-gradient(90deg, #EE348D 0%, #C72070 100%)
        border-radius 7px
        display flex
        justify-content center
        align-items center

        &::after
          content ''
          position absolute
          width 1px
          height 16px
          background #C82172
          z-index 999

      &-fill
        height 8px
        border-radius 5px 0 0 5px
        background #3E223F

      &-knob
        width 21px
        height 21px
        border-radius 50%
        background white
        box-shadow 2px 2px 5px rgba(0, 0, 0, 0.25)
        border 2px solid #EDEDED
        box-sizing border-box
        z-index 2

      &-inner
        height 21px
        position relative
        display flex
        align-items center

        &::after, &:before
          content ''
          position absolute
          height 8px
          width 50%
          z-index 1
          right 0

        &::after
          background #1E2036
          right 0

        &::before
          width calc(50% - 1px)
          left 0
          display none
          background #3E223F
</style>

<style lang="scss" scoped>
@import url('~vue-range-slider/dist/vue-range-slider.scss');
</style>
