<template lang="pug">
  .select-slider
    .button(@click="toggleSlide('down')")
      svg
        use(xlink:href="../../assets/images/character_editor/icons.svg#arrow")
    .select-slider__slides
      .text(v-for="(item, idx) in slides", :key="idx", :style="{ marginLeft: getSlidePos(idx) }") {{ item }}
    .button(style="transform: scale(-1, 1)", @click="toggleSlide('up')")
      svg
        use(xlink:href="../../assets/images/character_editor/icons.svg#arrow")
</template>

<script>
require('../../assets/style/form.stylus')

export default {
	props: ['slides', 'title'],
	components: {
	},
	data() {
		return {
			slide: 0
		}
	},

	created() {
		setTimeout(() => {
			const value = window.getInterface('CharacterEditor').getRandomValue(0, this.slides.length - 1);
			this.slide = value;
		}, 100);
	},

	methods: {
		toggleSlide(type) {
			if (type == 'up') {
				if (this.slide < this.slides.length - 1) this.slide++;
				else this.slide = 0;
			}

			if (type == 'down') {
				if (this.slide > 0) this.slide--;
				else this.slide = this.slides.length - 1;
			}

			this.$emit('slide', this.slide);

			let data = window.getInterface('CharacterEditor').getData(this.title);
      mp.trigger(window.getInterface('CharacterEditor').$data.METHOD_NAME, JSON.stringify(data));
		},

		getSlidePos(idx) {
			if (idx == 0)
				return -100 * this.slide + '%';
			else
				return 0;
		}
	}
};
</script>

<style lang="stylus" scoped>
.select-slider
	background alpha(#2C3B57, 0.25)
	height 55px
	border-radius 5px
	display flex
	justify-content space-between
	align-items center
	padding 0 15px

	&__slides
		width calc(100% - 62px)
		text-align center
		display flex
		overflow hidden

		.text
			min-width 100%
			color #A3B3CB
			font-size 16px
			transition-duration .25s

	.button
		width 31px
		height 31px
		opacity 0.5
		transition-duration .2s
		display flex
		justify-content center
		align-items center
		cursor pointer

		&:hover
			opacity 1
			transition-duration .2s

		svg
			width 10px
			height 15px
			fill #506482
</style>