<template>
	<div id="hud">
		<top ref="top" />
		<alerts ref="alerts" />
		<speedometer ref="speedometer" />
		<weather ref="weather" />
		<inventory v-if="inventoryVisible" @close="toggleInventory" />
		<bottom ref="bottom" />
	</div>
</template>

<script>
import Top from './hud/Top';
import Alerts from './hud/Alerts';
import Speedometer from './hud/Speedometer';
import Weather from './hud/Weather';
import Bottom from './hud/Bottom';
import Inventory from './hud/Inventory';

import { TimelineLite, gsap, Circ } from 'gsap';

export default {
	name: 'hud',
	components: {
		Top,
		Alerts,
		Speedometer,
		Weather,
		Bottom,
		Inventory
	},
	props: ['openParams'],
	data: () => {
		return {
			isVisible: false,
			inventoryVisible: false,
			speedometer: Object,
			speedometerBound: Object,
			bottom: Object,
			bottomBound: Object,
			difference: 0,
			duration: 0.5
		};
	},
	mounted() {
		this.speedometer = this.$refs.speedometer.$el;
		this.bottom = this.$refs.bottom.$el;

		this.updateBounding();
		window.addEventListener('resize', () => this.updateBounding());

		document.addEventListener('keypress', e => {
			if (e.code === 'KeyN') this.toggleInventory();
		});

		this.forceSpeedometer();

		this.$on('inventory-close', () => (this.inventoryVisible = false));
	},
	methods: {
		updateBounding() {
			this.speedometerBound = this.speedometer.getBoundingClientRect();
			const bound = document.documentElement.getBoundingClientRect();
			this.difference = bound.width - this.speedometerBound.x;
		},
		disableSpeedometer() {
			gsap.to(this.speedometer, this.duration, {
				opacity: 0,
				ease: Circ.out,
				y: '-10%'
			});
			gsap.to(this.bottom, this.duration, {
				x: this.difference,
				ease: Circ.out,
				delay: this.duration / 2
			});
		},
		enableSpeedometer() {
			gsap.to(this.bottom, this.duration, {
				x: 0,
				ease: Circ.out
			});
			gsap.to(this.speedometer, this.duration, {
				opacity: 1,
				y: 0,
				ease: Circ.out,
				delay: this.duration / 2
			});
		},
		forceSpeedometer() {
			if (this.isVisible) return;

			gsap.set(this.speedometer, {
				opacity: 0,
				y: '-10%'
			});
			gsap.set(this.bottom, {
				x: this.difference
			});
		},
		randomVisible() {
			this.isVisible = !this.isVisible;
		},
		toggleInventory(state = undefined) {
			this.inventoryVisible = state || !this.inventoryVisible;
		}
	},
	watch: {
		isVisible(bool) {
			bool ? this.enableSpeedometer() : this.disableSpeedometer();
		}
	}
};
</script>

<style lang="sass" scoped>
@import "~@/assets/sass/hud.sass"
</style>
