<template>
	<div id="weather">
		<div class="left" ref="block">
			<marquee scrollamount="3">
				<p class="town" ref="town">{{ town }}</p>
			</marquee>
		</div>
		<hr />
		<div class="right">
			<p class="temperature">{{ temperature }}°</p>
			<i class="icon" :class="getWeatherIcon()"></i>
		</div>
	</div>
</template>

<script>
export default {
	name: 'weather',
	data: () => {
		return {
			temperature: -4,
			weather: 'show',
			town: 'Бертон',
			name: 'Карсер-вэй',
			townMarquee: false,
			nameMarquee: false
		};
	},
	methods: {
		getWeatherIcon() {
			let icon = 'icon-';
			switch (this.weather) {
				case 'show':
					icon += this.weather;
					break;

				default:
					icon += 'show';
			}
			return icon;
		}
	}
};
</script>

<style lang="sass" scoped>
@import "~@/assets/sass/hud/weather"
</style>
