<template>
	<div id="top">
		<div class="main bg-dark">
			<div class="logo">
				<img src="@/assets/logo.svg" alt />
			</div>
			<hr />
			<div class="money">
				<div class="cash">
					<i class="icon icon-dollar"></i>
					<span>${{ moneyFormat(cash) }}</span>
				</div>
				<div class="bank">
					<i class="icon icon-credit-card"></i>
					<span>${{ moneyFormat(bank) }}</span>
				</div>
			</div>
		</div>
		<div class="group">
			<div class="info bg-dark">
				<i class="icon icon-globe"></i>
				<span>ID: {{ user_id }}</span>
			</div>
			<div class="clock bg-dark">
				<i class="icon icon-clock"></i>
				<p class="time">{{ clock.time }}</p>
				<p class="date">{{ clock.date }}</p>
			</div>
		</div>
		<div class="stars">
			<i
				class="icon icon-star"
				v-for="star in getStars()"
				:key="star.key"
				:class="{ 'color-orange': star.active }"
			></i>
		</div>
	</div>
</template>

<script>
export default {
	name: 'top',
	data: () => {
		return {
			user_id: 1000,
			cash: 99999999,
			bank: 99999999,
			stars: {
				total: 5,
				active: 3
			},
			clock: {
				time: '14:47',
				date: '12/01/2020'
			}
		};
	},
	mounted() {
		setInterval(() => this.changeClock(), 1000);
	},
	methods: {
		moneyFormat(money) {
			return money.toString().replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1 ');
		},
		getStars() {
			let stars = [];
			for (let index = 1; index <= this.stars.total; index++) {
				stars.push({
					key: index,
					active: index <= this.stars.active
				});
			}
			return stars;
		},
		changeClock() {
			const date = new Date();
			const hours = ('0' + date.getHours()).slice(-2);
			const seconds = ('0' + date.getSeconds()).slice(-2);
			const day = ('0' + date.getDay()).slice(-2);
			const month = ('0' + date.getMonth()).slice(-2);
			const year = date.getFullYear();
			this.clock.time = `${hours}:${seconds}`;
			this.clock.date = `${day}/${month}/${year}`;
		}
	}
};
</script>

<style lang="sass" scoped>
@import "~@/assets/sass/hud/top"
</style>
