<template>
	<div id="personal-items" class="wrapper">
		<div class="subject">
			<span>Личные вещи</span>
			<p>
				{{ items.length }}
				<span>/{{ maxItemsCount }}</span>
			</p>
		</div>
		<div class="items">
			<item
				v-for="(undefined, index) in maxItemsCount"
				:key="index"
				:item="items[index]"
				:disableDragging="!items[index]"
				:data-item-type="slots.personal"
			/>
		</div>
	</div>
</template>

<script>
import { usePersonalItems } from './compositions/PersonalItems';
import Item from './Item';
import gsap from 'gsap';

export default {
	name: 'personalItems',
	components: {
		Item
	},
	setup() {
		return { ...usePersonalItems() };
	}
};
</script>