import { ref } from '@vue/composition-api';
import $store from '@/store';

export function usePersonalItems() {
	const items = ref($store.getters['Inventory/personalItems']);
	const maxItemsCount = ref($store.getters['Inventory/maxPersonalItemsCount']);
	const slots = ref($store.getters['Inventory/slots']);

	return { items, maxItemsCount, slots };
}
