<template lang="pug">
	.signin
		.signin__form
			.signin__form-logo
				img(src="../assets/images/logo.svg")
			.form(:style="`transform: scale(${scale})`")
				.form-title Регистрация
				.form__content
					.form__input(:class="{ error: forms.username.state == 1 }")
						.form__input--icon
							img(src="../assets/images/form/user.svg")
						input(placeholder="Введите имя пользователя", v-model="forms.username.value")
						.form__input--state
							img(src="../assets/images/form/ok.svg", v-show="forms.username.state == 2")
							img(src="../assets/images/form/bad.svg", v-show="forms.username.state == 1")
							.form__input--hint(v-show="forms.username.state == 1") {{ forms.username.error }}
					.form__input(:class="{ error: forms.email.state == 1 }")
						.form__input--icon
							img(src="../assets/images/form/mail.svg")
						input(placeholder="Введите email", v-model="forms.email.value")
						.form__input--state
							img(src="../assets/images/form/ok.svg", v-show="forms.email.state == 2")
							img(src="../assets/images/form/bad.svg", v-show="forms.email.state == 1")
							.form__input--hint(v-show="forms.email.state == 1") {{ forms.email.error }}
					.form__input(:class="{ error: forms.password.state == 1 }")
						.form__input--icon
							img(src="../assets/images/form/password.svg")
						input(placeholder="Введите пароль", v-model="forms.password.value", :type="forms.password.isTextHidden ? 'password' : 'text'")
						.form__input--state
							img(src="../assets/images/form/bad.svg", v-show="forms.password.state == 1")
							img(src="../assets/images/form/show.svg", style="cursor: pointer", v-show="forms.password.state == 0", @mousedown="forms.password.isTextHidden = false", @mouseup="forms.password.isTextHidden = true")
							.form__input--hint(v-show="forms.password.state == 1") {{ forms.password.error }}
					.form__input(:class="{ error: forms.repPassword.state == 1 }")
						.form__input--icon
							img(src="../assets/images/form/password.svg")
						input(placeholder="Повторите пароль", v-model="forms.repPassword.value", :type="forms.repPassword.isTextHidden ? 'password' : 'text'")
						.form__input--state
							img(src="../assets/images/form/bad.svg", v-show="forms.repPassword.state == 1")
							img(src="../assets/images/form/show.svg", style="cursor: pointer", v-show="forms.repPassword.state == 0", @mousedown="forms.repPassword.isTextHidden = false", @mouseup="forms.repPassword.isTextHidden = true")
							.form__input--hint(v-show="forms.repPassword.state == 1") {{ forms.repPassword.error }}
					.signin__promo
						.form__input(:class="{ error: forms.promo.state == 1 }")
							input(placeholder="Введите промокод", v-model="forms.promo.value")
							.form__input--state
								img(src="../assets/images/form/bad.svg", v-show="forms.promo.state == 1")
					.form__button.submit(@click="signup()") Зарегистрироваться
					.signin__back(@click="back()")
						img(src="../assets/images/form/arrow-left.svg")
						.text Вернуться назад

		.signin__content
			.signin__slides
				.signin__slide(v-for="(slide, idx) in slides", :key="idx", :style="{ marginLeft: getSliderPos(idx) }")
					img(:src="require('../assets/images/signin/slides/' + slide.image)")
					.signin__content-data
						.title(v-html="slide.title")
						.text {{ slide.text }}
			.signin__slider-controls
				.button(v-for="idx in slides.length", :class="{ selected: idx - 1 == slide }", @click="slide = idx - 1")
</template>

<script>
import SignIn from './auth/SignIn.vue'
require('../assets/style/signin.stylus')
require('../assets/style/form.stylus')

export default {
	components: {
		SignIn
	},
	data() {
		return {
			scale: 1,

			slide: 0,
			slides: [
				{
					title: 'Приветствуем тебя,<br> легенда!',
					text: 'Руководство полностью открыто для общения и обсуждения различных тем с участниками, можете присылать свои мысли и наработки касательно геймплея в личные сообщения группы.',
					image: '1.jpg'
				},
				{
					title: 'Приветствуем тебя,<br> легенда!',
					text: 'Руководство полностью открыто для общения и обсуждения различных тем с участниками, можете присылать свои мысли и наработки касательно геймплея в личные сообщения группы.',
					image: '2.jpg'
				},
				{
					title: 'Приветствуем тебя,<br> легенда!',
					text: 'Руководство полностью открыто для общения и обсуждения различных тем с участниками, можете присылать свои мысли и наработки касательно геймплея в личные сообщения группы.',
					image: '1.jpg'
				}
			],

			forms: {
				username: {
					value: '',
					state: 0,
					error: ''
				},
				email: {
					value: '',
					state: 0,
					error: ''
				},
				password: {
					value: '',
					state: 0,
					error: '',
					isTextHidden: true
				},
				repPassword: {
					value: '',
					state: 0,
					error: '',
					isTextHidden: true
				},
				promo: {
					value: '',
					state: 0,
					error: '',
				}
			}
		}
	},

	created() {
		this.scale = (window.outerWidth + window.outerHeight) / 3000;

		setInterval(() => {
			if (this.slide < this.slides.length - 1)
				this.slide++;
			else
				this.slide = 0;
		}, 5000);
	},

	methods: {
		signup() {
			if (this.validate()) {
				const data = {
					username: this.forms.username.value,
					email: this.forms.email.value,
					password: this.forms.password.value,
					promo: this.forms.promo.value
				};
				mp.trigger('playerRegister', JSON.stringify(data));
			}
		},

		setError(key, value) {
			this.forms[key].error = value;
			this.forms[key].state = 1;
		},

		back() {
			window.toggleInterface('SignUp', false);
			window.toggleInterface('SignIn', true);
		},

		validate() {
			for (const key in this.forms) {
				if (key != 'promo')
					if (this.forms[key].state == 1 || this.forms[key].value.trim().length == 0)
						return false;
			}
			return true;
		},

		getSliderPos(idx) {
			if (idx == 0)
				return -100 * this.slide + '%';
			return 0;
		}
	},

	watch: {
		'forms.username.value'(val) {
			if (val.trim().length == 0)
				this.setError('username', 'Введите имя пользователя!');
			else {
				if (val.trim().length < 3)
					this.setError('username', 'Не менее 3-х символов!');
				else {
					const re = /[^A-z0-9]+/g;
					if (re.test(val))
						this.setError('username', 'Доспустимые символы A-z, 0-9!');
					else
						this.forms.username.state = 2;
				}
			}
		},
		'forms.email.value'(val) {
			if (!val.trim().length) {
				this.setError('email', 'Введите email!');
			} else {
				const re = /^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/;
				if (!re.test(val)) {
					this.setError('email', 'Неверный формат email!');
				} else
					this.forms.email.state = 2;
			}
		},
		'forms.password.value'(val) {
			if (val.trim().length < 6) {
				if (val.trim().length == 0)
					this.setError('password', 'Введите пароль!');
				else
					this.setError('password', 'Длина пароля не менее 6 символов!');
			} else {
				this.forms.password.state = 0;
			}
		},
		'forms.repPassword.value'(val) {
			if (!val.trim().length) {
				this.setError('repPassword', 'Повторите пароль!');
			} else {
				if (this.forms.repPassword.value != this.forms.password.value) {
					this.setError('repPassword', 'Пароли не совпадают!');
				} else
					this.forms.repPassword.state = 0;
			}
		}
	}
};
</script>

<style lang="stylus" scoped>
.submit
	background linear-gradient(105.67deg, #2982FE 0%, #1266DA 100%)

	&:hover
		background linear-gradient(105.67deg, #2982FE 0%, #1266DA 100%)

.signin
	.submit:hover
		background linear-gradient(-105.67deg, #2982fe 0%, #1266da 100%)

	&__promo
		background-image url('../assets/images/form/promo.png')
		background-size cover
		height 100px
		margin-top 20px
		display flex
		justify-content center
		align-items center

		.form__input
			background #121822
			border 1px solid #212B3B
			border-radius 5px
			height 50px
			width 305px
			text-align center
			box-sizing border-box
			font-size 17px
			color white
			padding 0 40px !important
			margin-top 0 !important
			position relative

			&--state
				margin 0
				position absolute
				right 10px

			input
				text-align center
				width 100% !important
				margin 0 !important
			
			&::placeholder
				color alpha(#506482, 0.5)

	&__back
		display flex
		align-items center
		color alpha(#506482, 0.8)
		font-size 17px
		cursor pointer
		margin-top 40px
		transition-duration .2s

		img
			opacity 0.8
			transition-duration .2s

		&:hover
			color #506482
			transition-duration .2s

			img
				opactiy 1
				transition-duration .2s

		.text
			margin-left 10px
</style>
