<template lang="pug">
  .range
    vue-range-slider(:min="min", :max="max", :step="1", v-model="value")
</template>

<script>
import VueRangeSlider from 'vue-range-slider'
require('../../assets/style/form.stylus')

export default {
  props: ['min', 'max', 'title'],
	components: {
    VueRangeSlider
	},
	data() {
		return {
      value: 1
		}
  },
  
  created() {
    setTimeout(() => {
      const value = window.getInterface('CharacterEditor').getRandomValue(0, this.max);
      this.value = value;
    }, 100);
  },

  watch: {
    value(val) {
      this.$emit('value', (val - this.max / 2) / this.max * 2);
      let data = window.getInterface('CharacterEditor').getData(this.title);
      mp.trigger(window.getInterface('CharacterEditor').$data.METHOD_NAME, JSON.stringify(data));
    }
  }
};
</script>

<style lang="stylus">
.range
  .range-slider
    &-inner
      height 29px
      display flex
      align-items center

    &-rail
      height 8px
      background alpha(#0671FF, 0.15)
      border-radius 10px

    &-fill
      height 8px
      background linear-gradient(136.23deg, #2982FE 0%, #1266DA 100%)
      border-radius 10px

    &-knob
      width 16px
      height 16px
      background white
      border 2px solid #EDEDED
      box-sizing border-box
      box-shadow 2px 2px 5px rgba(0, 0, 0, 0.25)

      &:hover
        width 21px
        height 21px
</style>

<style lang="scss" scoped>
@import url('~vue-range-slider/dist/vue-range-slider.scss');
</style>
