<template lang="pug">
	.ch-editor
		.ch-editor__modal-container(v-if="isShowSubmit")
			.ch-editor__modal(:style="`transform: scale(${scale})`")
				.title Вы уверены, что вы готовы<br> выйти в люди?
				.text Вы появитесь в новом для вас мире,в мире полном сюрпризов и разачерований. Только от вас зависит,кем вы будите,терпилой или бродягой!
				.buttons
					.form__button.submit(@click="create()") Да! - Погнали!
					.form__button(@click="isShowSubmit = false;") Нет! - Еще потыкаю

		.ch-editor__sidebar
			.ch-editor__sidebar--logo
				img(src="../assets/images/character_editor/logo.svg")
			.ch-editor__sidebar--buttons
				.buttons(v-for="(item, idx) in tabs", :key="idx")
					.button(:class="{ selected: tab == idx && targetTab == -1, noselectedtab: tab == idx && targetTab > -1 }", @click="selectTab(idx, -1)")
						svg
							use(:xlink:href="require('../assets/images/character_editor/icons.svg') + `#${item.name}`")
					.current(v-if="idx == tab")
						.button(v-for="(current, cid) in item.current", :key="cid", :class="{ selected: targetTab == cid }", @click="selectTab(idx, cid)")
							svg
								use(:xlink:href="require('../assets/images/character_editor/icons.svg') + `#${current.name}`")

		.ch-editor__content
			.ch-editor__title
				.icon
					svg
						use(v-if="targetTab == -1", :xlink:href="require('../assets/images/character_editor/icons.svg') + `#${tabs[tab].name}`")
						use(v-else, :xlink:href="require('../assets/images/character_editor/icons.svg') + `#${tabs[tab].current[targetTab].name}`")
				.text(v-if="targetTab == -1") {{ tabs[tab].title }}
				.text(v-if="targetTab > -1") {{ tabs[tab].current[targetTab].title }}
			.ch-editor__container
				dnk(ref="dnk", v-show="tab == 0")
				appearance(ref="appearance", v-show="tab == 1 && targetTab == -1")
				makeup(ref="makeup", v-show="tab == 1 && targetTab == 0")
				face(ref="face", v-show="tab == 2 && targetTab == -1")
				face-struct(ref="facestruct", v-show="tab == 2 && targetTab == 0")
				beard(ref="beard", v-show="tab == 3")
				clothes(ref="clothes", v-show="tab == 4")
			.ch-editor__buttons
				.form__button.submit(@click="isShowSubmit = true;") Создать персонажа
				.form__button(@click="random()")
					svg
						use(xlink:href="../assets/images/character_editor/icons.svg#random")
</template>

<script>
import SimpleBar from "simplebar";
import "simplebar/dist/simplebar.css";

import Dnk from '../components/character-editor/tabs/Dnk.vue'
import Appearance from '../components/character-editor/tabs/Appearance.vue'
import Makeup from '../components/character-editor/tabs/Makeup.vue'
import Face from '../components/character-editor/tabs/Face.vue'
import FaceStruct from '../components/character-editor/tabs/FaceStruct.vue'
import Beard from '../components/character-editor/tabs/Beard.vue'
import Clothes from '../components/character-editor/tabs/Clothes.vue'
require('../assets/style/form.stylus')

export default {
	name: 'characterEditor',
	components: {
		Dnk,
		Appearance,
		Makeup,
		Face,
		FaceStruct,
		Beard,
		Clothes
	},
	data() {
		return {
			METHOD_NAME: 'method',

			isShowSubmit: false,

			scale: 1,

			tab: 0,
			targetTab: -1,
			tabs: [
				{
					title: 'ДНК Персонажа',
					name: 'dnk',
					componentName: 'dnk',
					current: []
				},
				{
					title: 'Внешность',
					name: 'vneshnost',
					componentName: 'appearance',
					current: [
						{
							title: 'Макияж и цвет',
							componentName: 'makeup',
							name: 'color'
						}
					]
				},
				{
					title: 'Лицо',
					name: 'face',
					componentName: 'face',
					current: [
						{
							title: 'Строение лица',
							componentName: 'face-struct',
							name: 'faceStruct'
						}
					]
				},
				{
					title: 'Растительность',
					name: 'beard',
					componentName: 'beard',
					current: []
				},
				{
					title: 'Одежда',
					name: 'cloth',
					componentName: 'clothes',
					current: []
				}
			]
		}
	},

	created() {
		this.scale = (window.outerWidth + window.outerHeight) / 3000;

		if (!localStorage.characterSetting) localStorage.characterSetting = '{}';

		setTimeout(() => {
			mp.trigger('FirstRandom', JSON.stringify(this.getAllData()));
		}, 1000);
		
		mp.trigger('getWidth', window.outerWidth);
		mp.trigger('getHeight', window.outerHeight);
	},

	methods: {
		create() {
			this.isShowSubmit = false;
			mp.trigger('SaveAllData', JSON.stringify(this.getAllData()));
		},

		selectTab(idx, cid) {
			this.tab = idx; 
			this.targetTab = cid;
			mp.trigger('selectTab', idx, cid);
		},

		random() {
			mp.trigger('Random');
		},

		getRandomValue(min, max) {
			return Math.floor(Math.random() * (max - min) + min);
		},

		updateStaticParams(component, key, data) {
			this.$refs[component].$data.params[key] = JSON.parse(data);
		},

		getAllData() {
			let data = {};
			for (const key in this.$refs) {
				if (this.$refs[key])
					data[key] = this.$refs[key].$data;
			}
			return data;
		},

		getData(title) {
			let data = {};
			for (const key in this.$refs) {
				let name = this.tabs[this.tab].componentName;
				if (this.targetTab > -1)
					name = this.tabs[this.tab].current[this.targetTab].componentName;
				if (name == key) {
					if (this.$refs[key]) {
						data.changes = this.$refs[key].$data;
						data.changes.title = title;
						data.title = key;
					}
				}
			}
			return data;
		}
	}
};
</script>

<style lang="stylus" scoped>
.ch-editor
	display flex
	user-select none
	transform-origin left top

	&__title
		display flex
		align-items center
		height 80px
		background alpha(#212B3B, 0.25)
		padding 0 20px

		.icon
			width 40px
			height 40px
			display flex
			justify-content center
			align-items center
			text-align center
			margin-left 10px
			margin-top 10px

			svg
				width 40px
				height 40px
				fill #A3B3CB

		.text
			color #A3B3CB
			font-size 18px
			font-weight 500
			line-height 21px
			margin-left 10px

	&__buttons
		display grid
		grid-template-columns 258px 60px
		justify-content space-between
		padding 30px

		.form__button
			height 60px !important
			margin 0

			&.submit
				line-height 60px
				background linear-gradient(105.67deg, #2982FE 0%, #1266DA 100%)

				&:hover
					background linear-gradient(-105.67deg, #2982fe 0%, #1266da 100%)

			&:last-child
				display flex
				justify-content center
				align-items center

				svg
					width 24px
					height 24px
					fill #506482

			&:hover
				background #3a4b6a

	&__sidebar
		width 100px
		background #121822
		height 100vh

		&--logo
			width 100%
			height 80px
			background linear-gradient(92.87deg, #F9A041 0%, #FF5B5B 80.13%)
			display flex
			justify-content center
			align-items center

		&--buttons
			width 100%
			padding 30px 20px

			.buttons
				margin-top 20px
				display flex
				justify-content center
				flex-wrap wrap
				
				&:first-child
					margin 0

				.current
					.button
						margin-top 10px !important
						width 50px
						height 50px

						svg
							width 25px
							height 25px
							fill white

				.button
					width 100%
					height 60px
					background #212B3B
					border-radius 5px
					display flex
					justify-content center
					align-items center
					cursor pointer
					transition-duration .2s

					&.noselectedtab
						svg
							fill white

					svg
						width 30px
						height 30px
						fill #506482
						transition-duration .2s

					&.selected, &:hover
						background alpha(#FF5B5B, 0.25)
						transition-duration .2s
						
						svg
							fill #FF5B5B !important
							transition-duration .2s

	&__content
		width 395px
		background #151C29

	&__container
		height calc(100vh - 200px)
		max-height calc(100vh - 200px)
		padding 30px
		padding-bottom 0
		overflow-y auto
		overflow-x hidden

		&::-webkit-scrollbar 
			width 4px

		&::-webkit-scrollbar-track
			background #101620
			border-radius 5px

		&::-webkit-scrollbar-thumb
			border-radius 5px
			background #2E3846
</style>

<style lang="stylus">
.ch-editor
	&__modal
		width 625px
		box-sizing border-box
		padding 30px 68px
		background #151C29
		box-shadow 0px 2px 10px rgba(0, 0, 0, 0.25)
		border-radius 5px
		text-align center

		.title
			font-size 24px
			color #F9F9F9
			font-weight 500
			line-height 150%

		.text
			color #506482
			font-size 16px
			line-height 160%
			margin 20px 0

		.buttons
			display grid
			grid-template-columns repeat(2, 230px)
			justify-content space-between

		&-container
			position fixed
			width 100vw
			height 100vh
			display flex
			justify-content center
			align-items center
			background alpha(#19222F, 0.65)
			z-index 999

	.sex
		display grid
		grid-template-columns repeat(2, 160px)
		justify-content space-between

		.button
			background #212B3B
			border-radius 5px
			height 50px
			display flex
			justify-content center
			align-items center
			color #A3B3CB
			font-size 16px
			line-height 19px
			cursor pointer

			svg
				width 20px
				height 20px
				fill #A3B3CB
				margin-right 13px

			&, &.selected, &:hover
				transition-duration .2s

			&:first-child
				&.selected, &:hover
					background rgba(6, 113, 255, 0.15)
					color #2982FE

					svg
						fill #2982FE

			&:last-child
				&.selected, &:hover
					background alpha(#EE348D, 0.25)
					color #EE348D

					svg
						fill #EE348D

	.form__input
		margin-top 20px

		input
			width calc(100% - 44px)
			margin 0

	&__row
		padding 30px 0
		border-bottom 1px solid #212B3B

		&:first-child
			padding-top 0

		&-title
			color #506482
			font-size 16px
			line-height 19px
			margin-bottom 15px
			display flex
			justify-content space-between

			.value
				display flex

				.span
					color #0B6DEF

				.text
					font-size 12px
					line-height 21px
</style>
