<template>
	<div id="inventory">
		<div class="header">
			<p>Инвентарь</p>
			<i class="icon icon-inventory-close" @click="$emit('close')"></i>
		</div>
		<div class="content-group">
			<div class="content" ref="wrapper">
				<personal-items />
				<bag />
				<character-items />
				<environment-items />
			</div>
			<div class="footer">
				<div class="group" v-if="currentItem">
					<div class="item-name">
						<span>Название:</span>
						{{ currentItem.name }}
					</div>
					<div class="btn-group">
						<v-btn :disabled="!currentItem.use" depressed class="blue">Использовать</v-btn>
						<v-btn :disabled="!currentItem.remove" depressed class="red">Выбросить</v-btn>
						<v-btn :disabled="!currentItem.shift" depressed class="gray">Переложить</v-btn>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import { ref } from '@vue/composition-api';

import personalItems from './inventory/PersonalItems';
import Bag from './inventory/Bag';
import characterItems from './inventory/CharacterItems';
import environmentItems from './inventory/EnvironmentItems';

import $store from '@/store';

export default {
	name: 'inventory',
	components: {
		personalItems,
		Bag,
		characterItems,
		environmentItems
	},
	setup() {
		const wrapper = ref(null);
		const currentItem = ref($store.getters['Inventory/currentItem']);

		return { currentItem, wrapper };
	}
};
</script>