<template>
	<transition-group id="alerts" name="list" tag="div">
		<div class="alert" v-for="(alert) in alerts" :key="alert.id" :class="alert.type">
			<p>{{ alert.text }}{{ alert.id }}</p>
		</div>
	</transition-group>
</template>

<script>
export default {
	name: 'alerts',
	data: () => {
		return {
			types: ['success', 'warning', 'error', 'info'],
			alerts: [],
			removeDelay: 3000,
			lastInsertId: 0
		};
	},
	methods: {
		addAlert(text, type) {
			if (this.types.indexOf(type) === -1) return;

			this.lastInsertId++;

			this.alerts.push({
				id: this.lastInsertId,
				text,
				type
			});

			setTimeout(() => this.removeAlert(), this.removeDelay);
		},
		removeAlert(id) {
			this.alerts.shift();
		}
	}
};
</script>

<style lang="sass" scoped>
@import "~@/assets/sass/hud/alerts"
.list-item
	display: inline-block
	margin-right: 10px

.list-enter-active
	transition: opacity 0.5s, transform 1s

.list-leave-active
	transition: opacity 0.5s, transform 1s

.list-enter, .list-leave-to
	opacity: 0
	transform: translateY(30px)

.list-leave-to
	opacity: 0
	transform: translateX(-100%)

</style>
