<template>
	<div class="item inventory-cell">
		<div class="group">
			<i v-if="item && 'icon' in item" class="icon" :class="`icon-inventory-${item.icon}`"></i>
			<img v-else-if="item && 'img' in item" :src="loadImage(item.img)" alt />
		</div>
	</div>
</template>

<script>
import { useLoadImage } from './compositions/loadImage';

export default {
	name: 'item',
	props: {
		item: [Object, null]
	},
	setup() {
		return { ...useLoadImage() };
	}
};
</script>