<template lang="pug">
  .select-color
    .button(@click="toggleColor('down')", :style="{ width: size + 'px', height: size + 'px' }")
      svg
        use(xlink:href="../../assets/images/character_editor/icons.svg#arrow")
    .select-color__slides(:style="{ width: size == 30 ? '100%' : '210px' }")
      .color(v-for="(color, idx) in colors", :key="idx", :class="{ selected: idx == selected }", @click="selectColor(idx)" :style="{ background: color, marginLeft: getSliderPos(idx), minWidth: size + 'px', height: size + 'px' }")
    .button(style="transform: scale(-1, 1)", @click="toggleColor('up')", :style="{ width: size + 'px', height: size + 'px' }")
      svg
        use(xlink:href="../../assets/images/character_editor/icons.svg#arrow")
</template>

<script>
require('../../assets/style/form.stylus')

export default {
  props: ['colors', 'colorSize', 'title'],
	components: {
	},
	data() {
		return {
      size: 0,
      color: 0,
      selected: 0,
		}
  },
  created() {
    if (!this.colorSize) this.size = 30;
    else this.size = this.colorSize;

    setTimeout(() => {
      const value = window.getInterface('CharacterEditor').getRandomValue(0, this.colors.length - 1);
      this.selected = value;
    }, 100);
  },
	methods: {
    selectColor(idx) {
      this.selected = idx;
      this.$emit('color', this.colors[idx]);

      let data = window.getInterface('CharacterEditor').getData(this.title);
      mp.trigger(window.getInterface('CharacterEditor').$data.METHOD_NAME, JSON.stringify(data));
    },

    toggleColor(type) {
      if (type == 'up') {
        if (this.color < this.colors.length - 6) this.color++;
        else this.color = 0;
      }

      if (type == 'down') {
        if (this.color > 0) this.color--;
        else this.color = this.colors.length - 6;
      }
    },

    getSliderPos(idx) {
      let offset = 13;
      if (this.size != 30) offset = 12;

      if (idx == 0) return -(offset + this.size) * this.color + 'px';
      return offset + 'px';
    }
	}
};
</script>

<style lang="stylus" scoped>
.select-color
  display flex
  justify-content center

  &__slides
    display flex
    overflow hidden

    .color
      min-width 30px
      height 30px
      border-radius 5px
      box-sizing border-box
      margin-left 14px
      cursor pointer
      transition-duration .25s

      &:first-child
        margin 0

      &.selected, &:hover
        border 1px solid #F9F9F9

  .button
    min-width 30px
    height 30px
    display flex
    align-items center
    background #212B3B
    border-radius 5px
    box-sizing border-box
    padding 0 10px
    cursor pointer
    opacity 0.5
    transition-duration .2s

    &:first-child
      margin-right 13px

    &:last-child
      margin-left 13px

    &:hover
      opacity 1
      transition-duration .2s

    svg
      width 7px
      height 13px
      fill #506482
      margin-top -2px
</style>