<template lang="pug">
	.signin
		.signin__form
			.signin__form-logo
				img(src="../assets/images/logo.svg")
			.form(:style="`transform: scale(${scale})`")
				.form-title Авторизация
				.form__content
					.form__input(:class="{ error: forms.username.state == 1 }")
						.form__input--icon
							img(src="../assets/images/form/user.svg")
						input(placeholder="Введите имя пользователя", v-model="forms.username.value")
						.form__input--state
							img(src="../assets/images/form/ok.svg", v-show="forms.username.state == 2")
							img(src="../assets/images/form/bad.svg", v-show="forms.username.state == 1")
							.form__input--hint(v-show="forms.username.state == 1") {{ forms.username.error }}
					.form__input(:class="{ error: forms.password.state == 1 }")
						.form__input--icon
							img(src="../assets/images/form/password.svg")
						input(placeholder="Введите пароль", v-model="forms.password.value", :type="forms.password.isTextHidden ? 'password' : 'text'")
						.form__input--state
							img(src="../assets/images/form/bad.svg", v-show="forms.password.state == 1")
							img(src="../assets/images/form/show.svg", style="cursor: pointer", v-show="forms.password.state == 0", @mousedown="forms.password.isTextHidden = false", @mouseup="forms.password.isTextHidden = true")
							.form__input--hint(v-show="forms.password.state == 1") {{ forms.password.error }}
					.form__button.submit(@click="signin()") Войти в игру
					.form__button(@click="signup()") Регистрация
				.signin__social
					.signin__social--title
						.text Войти через социальную сеть
					.signin__social-buttons
						.button
							svg 
								use(xlink:href="../assets/images/form/social.svg#vk")
						.button
							svg 
								use(xlink:href="../assets/images/form/social.svg#gmail")
					.signin__social-text Забыл пароль? Не беда, 
						.button(@click="reset()") восстановить!

		.signin__content
			.signin__slides
				.signin__slide(v-for="(slide, idx) in slides", :key="idx", :style="{ marginLeft: getSliderPos(idx) }")
					img(:src="require('../assets/images/signin/slides/' + slide.image)")
					.signin__content-data
						.title(v-html="slide.title")
						.text {{ slide.text }}
			.signin__slider-controls
				.button(v-for="idx in slides.length", :class="{ selected: idx - 1 == slide }", @click="slide = idx - 1")
</template>

<script>
import SignIn from './auth/SignIn.vue'
require('../assets/style/signin.stylus')
require('../assets/style/form.stylus')

export default {
	components: {
		SignIn
	},
	data() {
		return {
			scale: 1,

			slide: 1,
			slides: [
				{
					title: 'Приветствуем тебя,<br> легенда!',
					text: 'Руководство полностью открыто для общения и обсуждения различных тем с участниками, можете присылать свои мысли и наработки касательно геймплея в личные сообщения группы.',
					image: '1.jpg'
				},
				{
					title: 'Приветствуем тебя,<br> легенда!',
					text: 'Руководство полностью открыто для общения и обсуждения различных тем с участниками, можете присылать свои мысли и наработки касательно геймплея в личные сообщения группы.',
					image: '2.jpg'
				},
				{
					title: 'Приветствуем тебя,<br> легенда!',
					text: 'Руководство полностью открыто для общения и обсуждения различных тем с участниками, можете присылать свои мысли и наработки касательно геймплея в личные сообщения группы.',
					image: '1.jpg'
				}
			],

			forms: {
				username: {
					value: '',
					state: 0,
					error: ''
				},
				password: {
					value: '',
					state: 0,
					error: '',
					isTextHidden: true
				}
			}
		}
	},

	created() {
		this.scale = (window.outerWidth + window.outerHeight) / 3000;
	},

	methods: {
		signin() {
			if (this.validate()) {
				const data = {
					username: this.forms.username.value,
					password: this.forms.password.value
				};
				mp.trigger('playerLogin', JSON.stringify(data));
			}
		},

		signup() {
			window.toggleInterface('SignIn', false);
			window.toggleInterface('SignUp', true);
		},

		reset() {
			window.toggleInterface('SignIn', false);
			window.toggleInterface('Reset', true);
		},

		setError(key, value) {
			this.forms[key].error = value;
			this.forms[key].state = 1;
		},

		validate() {
			for (const key in this.forms) {
				if (this.forms[key].state == 1 || this.forms[key].value.trim().length == 0)
					return false;
			}
			return true;
		},

		getSliderPos(idx) {
			if (idx == 0)
				return -100 * this.slide + '%';
			return 0;
		}
	},

	watch: {
		'forms.username.value'(val) {
			if (!val.trim().length) {
				this.setError('username', 'Введите имя пользователя!');
			} else {
				const re = /[^A-z0-9]+/g;
				if (re.test(val)) {
					this.setError('username', 'Доспустимые символы A-z, 0-9!');
				} else
					this.forms.username.state = 2;
			}
		},
		'forms.password.value'(val) {
			if (val.trim().length < 6) {
				if (val.trim().length == 0)
					this.setError('password', 'Введите пароль!');
				else
					this.setError('password', 'Длина пароля не менее 6 символов!');
			} else {
				this.forms.password.state = 0;
			}
		}
	}
};
</script>

<style lang="stylus" scoped>


</style>
