import { ref } from '@vue/composition-api';
import $store from '@/store';

export function useCharacterItems() {
	const items = ref($store.getters['Inventory/environmentItems']);
	const maxItemsCount = ref($store.getters['Inventory/maxEnvironmentItemsCount']);
	const cells = ref($store.getters['Inventory/characterCells']);
	const slots = ref($store.getters['Inventory/slots']);

	return { items, maxItemsCount, cells, slots };
}
