<template lang="pug">
	.signin
		.signin__form
			.signin__form-logo
				img(src="../assets/images/logo.svg")
			.form(:style="`transform: scale(${scale})`")
				.form-title Восстановить пароль
				.form__content(v-if="page == 0")
					.form__input(:class="{ error: forms.email.state == 1 }")
						.form__input--icon
							img(src="../assets/images/form/mail.svg")
						input(placeholder="Введите почту", v-model="forms.email.value")
						.form__input--state
							img(src="../assets/images/form/ok.svg", v-show="forms.email.state == 2")
							img(src="../assets/images/form/bad.svg", v-show="forms.email.state == 1")
							.form__input--hint(v-show="forms.email.state == 1") {{ forms.email.error }}
					.form__button.submit(@click="reset()") Отправить код
					.signin__back(@click="back()")
						img(src="../assets/images/form/arrow-left.svg")
						.text Вернуться назад
				.form__content(v-if="page == 1")
					.signin__reset-text1 Инструкция по восстановлению отправлена Вам на почту!
					.signin__reset-text2 Отправить повторно?
						.span(v-if="sendMessageTime > 0") Через {{ Math.floor(sendMessageTime / 60) }}:{{ sendMessageTime % 60 > 9 ? sendMessageTime % 60 : '0' + sendMessageTime % 60 }} сек
						.span(v-if="sendMessageTime <= 0", style="cursor: pointer", @click="repeatSend()") Отправить повторно
					.form__button.submit(@click="signin()") Авторизация
					.signin__back(@click="changeData()")
						img(src="../assets/images/form/arrow-left.svg")
						.text Смена данных

		.signin__content
			.signin__slides
				.signin__slide(v-for="(slide, idx) in slides", :key="idx", :style="{ marginLeft: getSliderPos(idx) }")
					img(:src="require('../assets/images/signin/slides/' + slide.image)")
					.signin__content-data
						.title(v-html="slide.title")
						.text {{ slide.text }}
			.signin__slider-controls
				.button(v-for="idx in slides.length", :class="{ selected: idx - 1 == slide }", @click="slide = idx - 1")
</template>

<script>
import SignIn from './auth/SignIn.vue'
require('../assets/style/signin.stylus')
require('../assets/style/form.stylus')

export default {
	components: {
		SignIn
	},
	data() {
		return {
			scale: 1,

			page: 0,

			sendMessageTime: 60,
			sendMessageTimer: null,

			slide: 1,
			slides: [
				{
					title: 'Приветствуем тебя,<br> легенда!',
					text: 'Руководство полностью открыто для общения и обсуждения различных тем с участниками, можете присылать свои мысли и наработки касательно геймплея в личные сообщения группы.',
					image: '1.jpg'
				},
				{
					title: 'Приветствуем тебя,<br> легенда!',
					text: 'Руководство полностью открыто для общения и обсуждения различных тем с участниками, можете присылать свои мысли и наработки касательно геймплея в личные сообщения группы.',
					image: '2.jpg'
				},
				{
					title: 'Приветствуем тебя,<br> легенда!',
					text: 'Руководство полностью открыто для общения и обсуждения различных тем с участниками, можете присылать свои мысли и наработки касательно геймплея в личные сообщения группы.',
					image: '1.jpg'
				}
			],

			forms: {
				email: {
					value: '',
					state: 0,
					error: ''
				}
			}
		}
	},

	created() {
		this.scale = (window.outerWidth + window.outerHeight) / 3000;
	},

	methods: {
		reset() {
			if (this.validate()) {
				this.page = 1;
				this.sendMessageTime = 60;
				this.sendMessageTimer = setInterval(() => {
					this.sendMessageTime--;
					if (this.sendMessageTime == 0)
						clearInterval(this.sendMessageTimer);
				}, 1000);
				const data = {
					email: this.forms.email.value
				};
				mp.trigger('playerReset', JSON.stringify(data));
			}
		},

		signin() {
			window.toggleInterface('Reset', false);
			window.toggleInterface('SignIn', true);
		},

		repeatSend() {
			const data = {
				email: this.forms.email
			};
			mp.trigger('playerRepeatReset', JSON.stringify(data));
		},

		setError(key, value) {
			this.forms[key].error = value;
			this.forms[key].state = 1;
		},

		back() {
			clearInterval(this.sendMessageTimer);
			this.signin();
		},

		changeData() {
			this.page = 0;
		},

		validate() {
			for (const key in this.forms) {
				if (this.forms[key].state == 1 || this.forms[key].value.trim().length == 0)
					return false;
			}
			return true;
		},

		getSliderPos(idx) {
			if (idx == 0)
				return -100 * this.slide + '%';
			return 0;
		}
	},

	watch: {
		'forms.email.value'(val) {
			if (!val.trim().length) {
				this.setError('email', 'Введите email!');
			} else {
				const re = /^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/;
				if (!re.test(val)) {
					this.setError('email', 'Неверный формат email!');
				} else
					this.forms.email.state = 2;
			}
		}
	}
};
</script>

<style lang="stylus" scoped>
.signin
	&__back
		display flex
		align-items center
		color alpha(#506482, 0.9)
		font-size 17px
		cursor pointer
		margin-top 40px
		transition-duration .2s

		img
			opacity 0.8
			transition-duration .2s

		&:hover
			color #506482
			transition-duration .2s

			img
				opactiy 1
				transition-duration .2s

		.text
			margin-left 10px

	&__form
		align-content flex-start !important

		.form
			margin-top 165px

	&__reset
		&-text1
			font-size 17px
			line-height 120%
			color #A3B3CB
			white-space nowrap

		&-text2
			color #506482
			font-size 17px
			line-height 120%
			font-weight 500
			display flex
			margin-top 20px

			.span
				color #F9F9F9
				margin-left 5px
</style>
