<template lang="pug">
  .dnk
    .ch-editor__row
      .sex
        .button(:class="{ selected: sex == 0 }", @click="sex = 0")
          svg
            use(xlink:href="../../../assets/images/character_editor/icons.svg#male")
          .text Мужской 
        .button(:class="{ selected: sex == 1 }", @click="sex = 1")
          svg
            use(xlink:href="../../../assets/images/character_editor/icons.svg#female")
          .text Женский
      .form__input(style="margin-top: 30px")
        input(placeholder="Введите имя", v-model="name.value")
        .form__input--state
          img(v-show="name.state == 2", src="../../../assets/images/form/ok.svg")
          img(v-show="name.state == 1", src="../../../assets/images/form/bad.svg")
      .form__input
        input(placeholder="Введите фамилию", v-model="surname.value")
        .form__input--state
          img(v-show="surname.state == 2", src="../../../assets/images/form/ok.svg")
          img(v-show="surname.state == 1", src="../../../assets/images/form/bad.svg")
    .ch-editor__row
      parents(@mother="(e) => this.mother = e", @father="(e) => this.father = e", title="parents")
    .ch-editor__row
      .dnk__parents-row
        .ch-editor__row-title Сходство
        range-sex(@value="(e) => this.similarity = e", title="similarity")
      .dnk__parents-row
        .ch-editor__row-title Цвет кожи
        range-sex(@value="(e) => this.skinColor = e", title="skinColor")
</template>

<script>
import Parents from '../Parents.vue'
import RangeSex from '../RangeSex.vue'
require('../../../assets/style/form.stylus')

export default {
	components: {
    Parents,
    RangeSex
	},
	data() {
		return {
      sex: 0,
      name: {
        value: '',
        error: '',
        state: 0
      },
      surname: {
        value: '',
        error: '',
        state: 0
      },
      mother: 0,
      father: 0,
      similarity: 0,
      skinColor: 0,
		}
	},

	methods: {
    setError(key, value) {
      this[key].state = 1;
      this[key].error = value;
    }
  },
  
  watch: {
    'name.value'(val) {
      if (val.length < 2) this.setError('name', 'Слишком короткое!');
      else {
        const re = /^[a-zA-Z]+$/;
        if (!re.test(val)) this.setError('name', 'Неправильный ввод!');
        else this.name.state = 2;
      }
    },

    'surname.value'(val) {
      if (val.length < 2) this.setError('surname', 'Слишком короткое!');
      else {
        const re = /^[a-zA-Z]+$/;
        if (!re.test(val)) this.setError('surname', 'Неправильный ввод!');
        else this.surname.state = 2;
      }
    },

    sex(val) {
      let data = {
        title: 'sex',
        changes: {
          sex: this.sex
        }
      };
      mp.trigger(window.getInterface('CharacterEditor').$data.METHOD_NAME, JSON.stringify(data));
    }
  }
};
</script>

<style lang="stylus" scoped>
.dnk
  &__parents
    &-row
      margin-top 30px

      &:first-child
        margin 0
</style>
