import Vue from 'vue';
import App from './App.vue';
import store from './store';

import VueCompositionApi from './plugins/composition-api';
import vuetify from './plugins/vuetify';
import Vuelidate from './plugins/vuelidate';

Vue.config.productionTip = false;

const app = new Vue({
	store,
	vuetify,
	render: h => h(App)
}).$mount('#app');

window.getInterface = (name) => {
	return app.$children[0].$refs[name] || false;
}

window.toggleInterface = (name, state, params = false) => {
	if (params)
		updateInterfaceParams(name, params);
	app.$children[0].$data.interfaces[name].show = state;
}

window.updateInterfaceParams = (name, params) => {
	app.$children[0].$data.interfaces[name].params = JSON.parse(params);
}

export default app;
