<template>
	<div id="bottom">
		<div class="microphone bg-dark">
			<i class="icon icon-microphone" :class="{ active: microphone.status }"></i>
			<kbd>{{ microphone.key }}</kbd>
		</div>
		<div class="organism bg-dark">
			<div class="food">
				<i class="icon icon-burger"></i>
				<svg width="80" height="4" viewBox="0 0 80 4" xmlns="http://www.w3.org/2000/svg">
					<rect width="80" height="4" rx="2" fill="#383941" />
					<rect :width="getFood()" height="4" rx="2" fill="#E29623" />
				</svg>
				<span>{{ getFood() }}</span>
			</div>
			<div class="water">
				<i class="icon icon-bottle"></i>
				<svg width="80" height="4" viewBox="0 0 80 4" xmlns="http://www.w3.org/2000/svg">
					<rect width="80" height="4" rx="2" fill="#383941" />
					<rect :width="getWater()" height="4" rx="2" fill="#236FE2" />
				</svg>
				<span>{{ getWater() }}</span>
			</div>
		</div>
	</div>
</template>

<script>
export default {
	name: 'bottom',
	data: () => {
		return {
			microphone: {
				status: false,
				key: 'n'
			},
			clock: {
				time: '14:47',
				date: '12/01/2020'
			},
			organism: {
				food: 56,
				water: 90
			}
		};
	},
	mounted() {
		document.addEventListener('keydown', this.downMicrophone);
		document.addEventListener('keyup', this.upMicrophone);

		this.changeClock();
		setInterval(() => this.changeClock(), 1000);
	},
	methods: {
		downMicrophone(e) {
			if (this.hasKey(e.code)) this.microphone.status = true;
		},
		upMicrophone(e) {
			if (this.microphone.status && this.hasKey(e.code)) this.microphone.status = false;
		},
		toKeyCode() {
			return 'key' + this.microphone.key;
		},
		hasKey(code) {
			return code.toLowerCase() === this.toKeyCode().toLowerCase();
		},
		getFood() {
			if (this.organism.food > 100) return '100%';
			if (this.organism.food < 0) return '0%';

			return this.organism.food + '%';
		},
		getWater() {
			if (this.organism.water > 100) return '100%';
			if (this.organism.water < 0) return '0%';

			return this.organism.water + '%';
		},
		changeClock() {
			const date = new Date();
			const hours = ('0' + date.getHours()).slice(-2);
			const seconds = ('0' + date.getSeconds()).slice(-2);
			const day = ('0' + date.getDay()).slice(-2);
			const month = ('0' + date.getMonth()).slice(-2);
			const year = date.getFullYear();
			this.clock.time = `${hours}:${seconds}`;
			this.clock.date = `${day}/${month}/${year}`;
		}
	}
};
</script>

<style lang="sass" scoped>
@import "~@/assets/sass/hud/bottom"
</style>
